import axios from 'axios';
import React, { useState, useEffect } from 'react';
import Login from './Login';
import Main from './Main';


const App = () => {
  const [isLogin, setIsLogin] = useState(false)
  
  //
  const onLogin = () => {
    setIsLogin(true);
  }

  //
  const logout = () => {
    setIsLogin(false);

    // return axios
    // .post('http://localhost:8080/logout')
    // .then((res)=> {
    //   setIsLogin(false)
    // })
    // .catch((err)=> {
    //   console.log(err.response.data)
    // })
  }

  const authHandler = () =>{  //   ,     
    return axios
    .get("http://localhost:8091/logincheck")
    .then((res)=>{
        console.log(res.data);
        onLogin();
    })
    .catch((err) => {
        console.log(err.res.data);
    });
}

const kakaoHandler = () => {
    return axios
    .get("http://localhost:8091/auth/kakao/callback")
    .then((res)=>{
        console.log(res.data.kakao_account.email);
        console.log(' ')
        onLogin();
    })
    .catch((err) => {
        console.log(err.res.data);
    });
}

useEffect(()=>{
    authHandler();
    //kakaoHandler();
});


  return (
    <div>
      {isLogin ?
        <Main isLogin={isLogin} logout={logout}/> : 
        <Login isLogin={isLogin} onLogin={onLogin} />}
    </div>
  )
}

export default App;
